* {
    margin: 0;
    padding: 0;
    font-family: Gotham Pro;
}

::-webkit-scrollbar {
    display: none;
}

.page{
    min-height: 100vh;
}
.page>:first-child{
    margin-top: 10vh;
}

.main-block-title{
    margin-left: 6.8vw;
}

body {
    background: #1B1E23;
}

/*    site header   */
.header-title {
    width: 100vw;
    height: 10vh;

    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-content: center;
    align-items: center;

    position: fixed;
    top: 0;
    left: 2.14vw;
    
    z-index: 15;
    background: #1B1E23;
    /* margin-bottom: 4.28vh; */
}

.head-logo {
    width: 7.5vw;
}

.buttons {
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;

    cursor: pointer;

    margin-right: 2.9vw;
    white-space: nowrap;

    font-family: Gotham Pro;
    font-style: bold;
    font-size: 2vh;

}

.buttons>*:hover{
    opacity: 0.7;
}

.buttons>:first-child {
    color: #FFF700;

    margin-right: 3.8vw;
}

.buttons>:last-child {
    background-color: #FFF700;
    

    width: 7.3vw;
    height: 5.14vh;
    text-align: center;

    display: flex;
    align-items: center;
    justify-content: center;

    border-radius: 1vh;
}


.pagination{
    width: 18vw;
    margin: auto;

    position: fixed;
    bottom: 3.4vh;

    display: flex;

    flex-direction: row;
    justify-content: space-between;
    align-items: flex-start;
    z-index: 5;
}
.pagination>div{
    width: 100%;
    height: 3vw;

    display: flex;
    justify-content: center;

    cursor: pointer;
}
.pagination>div>a>div{
    width: max-content;
    height: max-content;
}
.dot-pag{
    margin-top: 1vh;
}

.pagination>div>a>.pag-elem{
    width: 4.16vw;
    height: 100%;

    display: flex;
    justify-content: center;
}
.pagination-mouse>svg>g:first-child{
    fill: none;
}


/* mobile */

@media screen and (max-width:460px){
    .header-title {
        width: 100vw;
        height: 10vh;
    
        display: flex;
        flex-direction: row;
        justify-content: space-between;
        align-content: center;
        align-items: center;
    
        position: fixed;
        top: 0;
        left: 0;

        padding-left: 4vw;
        
        z-index: 15;
        background: #1B1E23;
        
    }
    .pagination{
        display: none;
    }
    .head-logo {
        width: 23.88vw;
    }
    .buttons {
        display: flex;
        flex-direction: row;
        justify-content: center;
        align-items: center;
    
        cursor: pointer;
    
        margin-right: 2.9vw;
        white-space: nowrap;
    
        font-family: Gotham Pro;
        font-style: bold;
        font-size: 2vh;
    
    }
    
    .buttons>*:hover{
        opacity: 0.7;
    }
    
    .buttons>:first-child {
        color: #FFF700;
    
        margin-right: 3.8vw;
    }
    
    .buttons>:last-child {
        background-color: #FFF700;
        
    
        width: 27.78vw;
        height: 5.62vh;
        text-align: center;
    
        display: flex;
        align-items: center;
        justify-content: center;
    
        border-radius: 1vh;
    }
}
@media screen and (max-width: 700px){
    .pagination{
        display: none;
    }
}